import { useEffect } from 'react';
import {
    CardMedia,
    Grid,
    Typography,
    Card,
    CardContent,
    makeStyles,
    Fab,
    Divider
} from '@material-ui/core';
import { isMobile } from 'react-device-detect';
import ciudadsf from '../../assets/city/ciudadsf.jpg';
import ruta11 from '../../assets/city/ruta11.png';
import ciclovia from '../../assets/city/ciclovia.jpg';
import fumigacion from '../../assets/city/fumigacion.jpeg';
import transporte from '../../assets/city/sustentable.jpg';
import colores from '../../styles/colors';
import Loader from '../loader';
import useProgressiveImage from '../../helpers/progressiveImg';
import ReactGA from 'react-ga';
import jorCiudad from '../../assets/city/jorprovciudad.jpg';
import basura from '../../assets/city/basu2.jpg';
import hospveterpubl from '../../assets/city/hospveterpubl.JPG';
import DirectionsBikeIcon from '@material-ui/icons/DirectionsBike';
import DirectionsBusIcon from '@material-ui/icons/DirectionsBus';
import WbIncandescentIcon from '@material-ui/icons/WbIncandescent';
import FlashOnIcon from '@material-ui/icons/FlashOn';
const CityContent = ({ data }) => {
    const classes = useStyles();
    const imag = useProgressiveImage(ciudadsf);
    const Iframe = (
        <Card elevation={5} style={{ height: 550, borderRadius: 15 }}>
            <iframe
                src="https://www.google.com/maps/d/u/0/embed?mid=1tdp48Zmz7FqZvtO4sXOQv_s4uUFAe01p&amp;z=12"
                width="100%"
                height="100%"
            />
        </Card>
    );

    useEffect(() => ReactGA.modalview('/ciudad'), []);
    if (!imag) {
        return <Loader />;
    }
    return (
        <div className={classes.marginContainer}>
            <Grid container spacing={5}>
                <Grid item xs={12} sm={6}>
                    <Card elevation={3} className={classes.cardImgPr}>
                        <CardMedia
                            image={imag}
                            style={{
                                height: '100%',
                                borderRadius: 15
                            }}
                        />
                    </Card>
                </Grid>
                <Grid item xs={12} sm={6}>
                    <Typography className={classes.typograf}>
                        Las ciudades actuales tienen situaciones que son un gran
                        desafío para el ambiente, y está comprobado que la
                        calidad de vida dentro de las ciudades es contradictoria
                        y desigual. Es necesario integrar parámetros ambientales
                        en la planificación y gestión de nuestra ciudad para
                        lograr que tenga un funcionamiento más sostenible. En
                        este sentido, Santa Fe debe transformar sus formas y
                        funcionalidades urbanas para alcanzar una buena gestión
                        territorial. El desarrollo de la ciudad que queremos
                        contiene de manera implícita aspectos de sustentabilidad
                        en todas sus acciones, para lograr una administración
                        eficiente y racional de los bienes naturales en el largo
                        plazo, bajo los principios de la cohesión económica,
                        social y territorial, protegiendo el ambiente y
                        respetando nuestra diversidad cultural. Trabajamos en
                        proyectos que transformen nuestra ciudad en la capital
                        que soñamos, una Santa Fe sustentable, productiva,
                        dinámica, abierta y segura.
                    </Typography>
                </Grid>
                <Grid item xs={12} sm={8}>
                    {Iframe}
                </Grid>
                <Grid item xs={12} sm={4}>
                    <Card
                        elevation={3}
                        style={{ height: 550, padding: 3, borderRadius: 15 }}
                    >
                        <CardMedia
                            image={jorCiudad}
                            style={{
                                height: '100%',
                                borderRadius: 15
                            }}
                        />
                    </Card>
                </Grid>
                <Grid item xs={12}>
                    <Divider
                        variant="middle"
                        color="black"
                        style={{ margin: 20 }}
                    />
                    <Typography style={{ textAlign: 'center', fontSize: 32 }}>
                        Mis propuestas
                    </Typography>
                </Grid>
                <Grid item xs={12} sm={6}>
                    <Grid container spacing={3}>
                        <Grid item xs={12}>
                            <Typography
                                style={{
                                    textAlign: 'center',
                                    fontSize: 24,
                                    minHeight: 60
                                }}
                            >
                                Una ciudad iluminada es una ciudad más segura
                            </Typography>
                        </Grid>
                        {iluminacionData.map((card, i) => (
                            <Grid key={i} item xs={12} sm={6}>
                                <CardL data={card} />
                            </Grid>
                        ))}
                    </Grid>
                </Grid>
                <Grid item xs={12} sm={6}>
                    <Grid container spacing={3}>
                        <Grid item xs={12}>
                            <Typography
                                style={{
                                    textAlign: 'center',
                                    fontSize: 24,
                                    minHeight: 60
                                }}
                            >
                                Movilidad Sustentable
                            </Typography>
                        </Grid>
                        {movilidadData.map((card, i) => (
                            <Grid key={i} item xs={12} sm={6}>
                                <CardL data={card} />
                            </Grid>
                        ))}
                    </Grid>
                </Grid>{' '}
                <Grid item xs={12} sm={12}>
                    <Typography
                        style={{
                            textAlign: 'center',
                            fontSize: 22
                        }}
                    >
                        Santa Fe Sustentable: Erradicación de basurales y
                        microbasurales a cielo abierto
                    </Typography>
                </Grid>
                <Grid item xs={12} sm={6}>
                    <Card
                        elevation={3}
                        style={{ height: 280, borderRadius: 15, padding: 3 }}
                    >
                        <CardMedia
                            image={basura}
                            style={{
                                height: '100%',
                                borderRadius: 15
                            }}
                        />
                    </Card>
                </Grid>
                <Grid item xs={12} sm={6}>
                    <Typography>
                        Nueva Red de Recolección, Reciclado y Reutilización de
                        Residuos Frente al problema creciente de la basura en la
                        ciudad, impulsamos la creación de una Red sustentable y
                        segura de Recolección, Reciclado y Reutilización de
                        Residuos, con el objetivo de erradicar los
                        microbasurales a cielo abierto que se generan de manera
                        indiscriminada en múltiples puntos de Santa Fe. La
                        basura es un gran problema en una ciudad como la
                        nuestra, que está creciendo muchísimo, y la generación
                        de microbasurales es constante. Queremos concientizar
                        acerca de la importancia de una ciudad limpia, porque es
                        sinónimo de salud, higiene y espacios seguros para todos
                        los vecinos y vecinas de la ciudad.
                    </Typography>
                </Grid>
                <Grid item xs={12}>
                    <Typography style={{ textAlign: 'center', fontSize: 22 }}>
                        Emprender Santa Fe: Más trabajo, más educación, más
                        deportes y más cultura.
                    </Typography>
                </Grid>
                {emprenderData.map((card, i) => (
                    <Grid key={i} item xs={12} sm={4}>
                        <CardT data={card} />
                    </Grid>
                ))}
                <Grid item xs={12}>
                    <Typography style={{ textAlign: 'center', fontSize: 22 }}>
                        Hospital Veterinario Público
                    </Typography>
                </Grid>
                <Grid item xs={12} sm={6}>
                    <Typography className={classes.typograf}>
                        Junto al senador por el departamento La Capital, Marcos
                        Castelló, ingresamos una iniciativa para concretar la
                        creación de un Hospital Veterinario Público para Santa
                        Fe y su área metropolitana. Queremos transitar un camino
                        que consideramos más que justo, pero, sobre todo,
                        entendemos que se trata de una política de estado. El
                        proyecto que compartimos con Marcos tiene el objetivo de
                        crear un centro hospitalario que realice diagnósticos,
                        tratamientos ambulatorios, intervenciones mayores,
                        esterilizaciones, vacunaciones, a muy bajo costo y de
                        manera gratuita cuando la situación, el contexto y la
                        condición de salud del animal lo requieran.
                    </Typography>
                </Grid>
                <Grid item xs={12} sm={6}>
                    <Card
                        elevation={3}
                        style={{ height: 280, borderRadius: 15, padding: 3 }}
                    >
                        <CardMedia
                            image={hospveterpubl}
                            style={{
                                height: '100%',
                                borderRadius: 15
                            }}
                        />
                    </Card>
                </Grid>
            </Grid>
        </div>
    );
};

const CardL = ({ data }) => {
    const classes = useStyles();
    return (
        <Card elevation={3} className={classes.cardMed}>
            <div className={classes.cardLicondiv}>
                <div>
                    <Fab
                        style={{
                            cursor: 'default',
                            marginBottom: 15,
                            marginTop: 15
                        }}
                        size="small"
                    >
                        {data.ico}
                    </Fab>
                </div>
                <Typography className={classes.cardTitle}>
                    {data.title}
                </Typography>
            </div>

            <CardContent className={classes.cardContt}>
                <Typography className={classes.typograf}>
                    {data.text}
                </Typography>
            </CardContent>
        </Card>
    );
};

const CardT = ({ data }) => {
    const classes = useStyles();
    return (
        <Card elevation={3} className={classes.cardMed}>
            <div className={classes.cardLicondiv}>
                <Typography className={classes.cardTitle}>
                    {data.title}
                </Typography>
            </div>

            <CardContent className={classes.cardContt}>
                <Typography className={classes.typograf}>
                    {data.text}
                </Typography>
            </CardContent>
        </Card>
    );
};

const iluminacionData = [
    {
        id: 0,
        title: 'Plan de iluminación para avenidas y calles de tu barrio',
        text: 'Frente a las demandas de vecinas y vecinos, gestionamos con la provincia la inversión de 44 millones de pesos para luminarias en la ciudad.',
        ico: <WbIncandescentIcon style={{ fontSize: 25, color: '#324752' }} />,
        img: ruta11
    },
    {
        id: 1,
        title: 'Iluminación para la Circunvalación Oeste',
        text: 'Trabajamos junto al gobierno provincial y la Nación en un plan para iluminar la circunvalación Oeste de la ciudad.',
        ico: <FlashOnIcon style={{ fontSize: 25, color: '#324752' }} />,
        img: ciclovia
    }
];

const movilidadData = [
    {
        id: 0,
        title: 'Ciclovías más seguras',
        text: ` Trabajamos mucho para que haya ciclovías en la ciudad y ahora debemos lograr  que sea seguro transitarlas, principalmente por las noches. Estamos trabajando en proyectos para iluminar nuestras ciclovías, que también va a significar fomentar el uso de este medio de transporte que cuida el ambiente y favorece la salud`,
        ico: <DirectionsBikeIcon style={{ fontSize: 25, color: '#324752' }} />,
        img: ruta11
    },
    {
        id: 1,
        title: 'Paradas Seguras de Colectivos',
        text: `“Queremos que las paradas de colectivos sean espacios seguros, especialmente durante la noche”, sostuvo Jorgelina Mudallel. La concejala trabaja en proyectos para iluminar, informatizar, refaccionar y mantener higienizadas las paradas del transporte público en la ciudad.`,
        ico: <DirectionsBusIcon style={{ fontSize: 25, color: '#324752' }} />,
        img: ciclovia
    }
];

const emprenderData = [
    {
        id: 0,
        title: 'Oportunidades de capacitación y más créditos para emprendedores.',
        text: `Venimos trabajando, desde los peores momentos de la pandemia, junto a emprendedores locales en brindarles capacitaciones y todo el asesoramiento necesario para que puedan acceder a los créditos que otorga la provincia y sostener sus emprendimientos. Vamos a seguir en ese camino, de fortalecer a nuestros emprendedores y emprendedoras con capacitación, asistencia técnica para el acceso al crédito y espacios de promoción y venta.`,
        ico: <DirectionsBikeIcon style={{ fontSize: 25, color: '#324752' }} />,
        img: ruta11
    },
    {
        id: 1,
        title: 'Nueva Incubadora de emprendedores. ',
        text: `Hay muy buenas ideas en Santa Fe, vinculadas a la innovación, a la industria del conocimiento y eso es lo que tenemos que potenciar y acompañar, por eso estamos impulsando la Incubadora de Emprendedores de nuestra ciudad, un espacio para el encuentro, la formación y el crecimiento de emprendimientos y micropymes de la ciudad`,
        ico: <DirectionsBusIcon style={{ fontSize: 25, color: '#324752' }} />,
        img: ciclovia
    },
    {
        id: 2,
        title: 'Más espacios culturales y deportivos en los barrios',
        text: `Vamos a concretar en poco tiempo el primer Skate Park en barrio Las Flores, para garantizar un espacio de esparcimiento para nuestras juventudes. 
        Recientemente, la provincia inauguró un nuevo centro cultural en barrio Cabaña Leiva, y en poco tiempo llevaremos adelante un nuevo centro cultural en barrio Pompeya.
        Después de muchos años donde la cultura y el acceso a los espacios de esparcimiento estaban concentrados en el centro y los bulevares de la ciudad, hoy nuestra gestión provincial está presente llevando cultura y obras emblemáticas a los barrios, que es donde siempre debió haber estado.`,
        ico: <DirectionsBusIcon style={{ fontSize: 25, color: '#324752' }} />,
        img: ciclovia
    }
];

const useStyles = makeStyles((theme) => ({
    marginContainer: {
        width: isMobile && '94%',
        margin: isMobile && 'auto',
        paddingTop: 25,
        paddingBottom: 30
    },
    typograf: {
        textAlign: isMobile && 'center',
        color: colores.darkBlueNav,
        fontSize: 16
    },
    cardImgPr: {
        height: isMobile ? 200 : 300,
        borderRadius: 15,
        padding: 3
    },
    cardPadd: {
        height: !isMobile && 280,
        padding: '10px 20px',
        overflow: 'auto'
    },
    cardMed: {
        height: !isMobile && 380,
        borderRadius: 15
    },
    mediaico: {
        height: 120
    },
    cardLicondiv: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        margin: 'auto',
        height: '30%',
        background: '#547687',
        padding: 20
    },
    cardTitle: {
        color: '#f2f2f2',
        textAlign: 'center'
    },
    cardContt: {
        height: '70%',
        overflow: 'auto'
    }
}));

export default CityContent;
